.logo-water {
    position: absolute;
    width: 600px;
    opacity: 80%;
    filter: drop-shadow(0 0 0.75rem rgb(210, 255, 12));
}

.logo-gauge-container {
    position: absolute;
    width: 600px;
    margin-left: 200px;
    border-radius: 50%;
}

.logo-gauge {
    width: 225px;
    opacity: 80%;
}

.glass {
    position: absolute;
    width: 550px;
    margin-left: 25px;
    opacity: 60%;
}

.gauge-glass {
    position: absolute;
    width: 550px;
    margin-left: 25px;
    opacity: 60%;
}

.value-text {
    font-size: 6rem;
    color: #7fd705;
    display: flex;
    align-items: center;
    font-weight: bold;
}

.gauge-logo {
    width: 175px;
}

.gauge-card {
    border-radius: 15px;
    background: #ffffff;
    ;
    box-shadow: 3px 3px 5px #e2e0e0, -33px -33px 66px #ffffff;
    width: 400px;
    padding: 20px;
    margin-bottom: 50px;
    color: #7fd705;
    font-weight: bold;
    font-size: 1.5rem;
}

.gauge-input {
    border-radius: 20px;
    border: none;
    background-color: rgb(248, 248, 248);
    width: 400px;
    height: 50px;
    padding-left: 20px;
    font-size: 26px;
    text-align: center;
    font-weight: bold;
    color: black;
}

.modal-btn-title {
    margin-top: 50px;
    font-weight: bold;
    margin-bottom: 50px;
    font-size: 52px;
    color: #7fd705;
}

.modal-btn-heading {
    margin-top: 80px;
    text-align: right;
    font-size: 52px;
    font-weight: bold;
    margin-bottom: 50px;
    color: grey;
}

.modal-btn-text {
    font-size: 23px;
}

.modal-btn-top {
    border: 2px solid #7fd705;
    background-color: white;
    font-weight: bold;
    font-size: 26px;
    color: #7fd705;
    width: 100%;
    margin-top: 50px;
    width: 200px;
    height: 50px;
    border-radius: 15px;
    transition: all ease 0.2s;
}

.modal-btn-top:hover {
    width: 220px;
    height: 60px;
    border: 4px solid #7fd705;
}

.modal-btn {
    border: 2px solid grey;
    background-color: white;
    font-weight: bold;
    font-size: 26px;
    color: grey;
    width: 100%;
    margin-top: 50px;
    width: 200px;
    height: 50px;
    border-radius: 15px;
    transition: all ease 0.2s;
}

.modal-btn:hover {
    width: 220px;
    height: 60px;
    border: 4px solid grey;
}

.gauge-btn {
    border: 2px solid #7fd705;
    background-color: white;
    font-weight: bold;
    font-size: 26px;
    color: #7fd705;
    width: 100%;
    margin-top: 40px;
    width: 400px;
    height: 50px;
    border-radius: 50px;
    transition: all ease 0.5s;
}

.gauge-btn-withdraw {
    border: 2px solid grey;
    background-color: white;
    font-weight: bold;
    font-size: 26px;
    color: grey;
    width: 100%;
    margin-top: 20px;
    width: 400px;
    height: 50px;
    border-radius: 50px;
    pointer-events: none;
    transition: all ease 0.5s;
}

.gauge-btn:hover {
    border: 5px solid #7fd705;
}

.exit-modal:hover {
    color: crimson;
    cursor: pointer;
}

.gauge-timer {
    font-size: 40px;
    width: 400px;
}

.gauge-timer>span {
    font-size: 12px;
}

@media only screen and (max-width: 1000px) {
    .logo-water {
        width: 300px;
    }
    .glass {
        width: 275px;
        margin-left: 13px;
    }
    .gauge-input {
        width: 300px;
    }
    .gauge-btn {
        width: 300px;
    }
    .gauge-timer {
        font-size: 16px;
        width: 300px;
    }
    .gauge-glass {
        width: 275px;
        margin-left: 13px;
    }
    .logo-gauge-container {
        margin-left: 90px;
    }
    .logo-gauge {
        width: 125px;
    }
}